<style lang="scss">
@import "../../assets/scss/variables";

// Works Detail - Body -- Post
.detail-acticle {
    width: 100%;
    font-size: 1rem;
    line-height: 1.5;
    letter-spacing: 0.3px;
    margin-bottom: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (max-width: 575.98px) {
      position: relative;
      margin-top: 20px;
    }

    // Works Detail - Body -- Post style
    .post-wrap {
      width: 100%;
      padding: 0 10px 10px;

      // Post style - Text
      h2, h3, h4, h5, h6 {
        line-height: 1.3;
        letter-spacing: 1px;
        margin: 40px 0 20px;

        @media screen and (max-width: 575.98px) {
          margin: 30px 0 10px;
        }

        &:first-child {
          margin-top: 0;
        }
      }

      h2 {
        font-size: 1.75rem;

        @media screen and (max-width: 575.98px) {
          font-size: 1.5rem;
        }
      }

      h3 {
        color: rgba($color-deep-brown, 0.6);
        font-size: 1.625rem;

        @media screen and (max-width: 575.98px) {
          font-size: 1.375rem;
        }

        &::before {
          content: "# ";
        }
      }

      h4 {
        font-size: 1.375rem;

        @media screen and (max-width: 575.98px) {
          font-size: 1.125rem;
        }
      }

      h5 {
        color: $color-brown;
        font-size: 1.25rem;

        @media screen and (max-width: 575.98px) {
          font-size: 1rem;
        }
      }

      h6 {
        font-size: 1.125rem;

        @media screen and (max-width: 575.98px) {
          font-size: 0.875rem;
        }
      }

      // Post style - paragraph
      p {
        color: rgba($color-black, 0.9);

        @media screen and (max-width: 575.98px) {
          font-size: 0.875rem;
        }
      }

      li {
        color: rgba($color-black, 0.9);

        @media screen and (max-width: 575.98px) {
          font-size: 0.875rem;
        }
      }

      // Post style - list
      ol,
      ul {
        padding-left: 20px;

        & > li + li {
          margin-top: 2px;
        }
      }

      // Post style - sub list
      ol ol, ol ul,
      ul ol, ul ul {
        padding-left: 22px;

        li {
          margin: 4px 0;
        }
      }

      // Post style - link
      a {
        text-underline-offset: var(--offset, 1px);
        transition: --offset 300ms;

        text-decoration: underline;
        color: $color-black;
        opacity: 0.5;
        font-size: 1rem;
        line-height: 1.5;
        letter-spacing: 0.3px;
        word-break: break-all;

        @media screen and (max-width: 575.98px) {
          font-size: 0.8125rem;
        }

        &:hover {
          --offset: 5px;
        }
      }

      // Post style - block area

      // Post style - block area -- list
      ul.block-wrap,
      ol.block-wrap {
        padding: 18px 40px;

        @media screen and (max-width: 575.98px) {
          padding: 14px 15px 12px 35px;
        }
      }

      // Post style - block area -- box
      .block-wrap {
        color: rgba($color-black, 0.9);
        background-color: rgba($color-black, 0.07);
        padding: 18px 30px;
        border-radius: 10px;

        @media screen and (max-width: 575.98px) {
          font-size: 0.8125rem;
          padding: 14px 15px 12px 18px;
        }

        ul,
        ol,
        p {
          margin: 0;
        }

        li {
          @media screen and (max-width: 575.98px) {
            font-size: 0.8125rem;
          }
        }
      }

      // Post style - inline area
      .inline-wrap {
        background-color: rgba($color-black, 0.07);
        color: rgba($color-black, 0.7);
        font-size: 0.9375rem;
        padding: 1.5px 6px;
        border-radius: 3px;
        line-height: 1.8;
      }

      // Post style - image
      img {
        width: 100%;
      }

      // Post style - image layout -- 100%
      .img-wrap {
        display: inline-block;
        margin-bottom: 30px;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);

        // image layout -- 1 column (center)
        &.column-1 {
          width: 450px;
          margin: 0 auto 30px;
          display: block;

          @media screen and (max-width: 480px) {
            width: 100%;
          }
        }

        // image layout -- 2 column
        &.column-2 {
          width: calc((99% - 20px) / 2);

          @media screen and (max-width: 480px) {
            width: 100%;

            &:nth-child(odd) {
              margin-right: 0;
            }
          }

          &:nth-child(even) {
            margin-right: 20px;
          }
        }

        // image layout -- 3 column
        &.column-3 {
          width: calc((99% - 25px) / 3);

          &:nth-child(even) {
            margin-left: 10px;
            margin-right: 10px;
          }

          @media screen and (max-width: 480px) {
            width: 100%;

            &:nth-child(even) {
              margin-left: 0;
              margin-right: 0;
            }
          }
        }
      }

      // Post style - image layout --grid
      .img-grid-wrap {
        // pc
        @media screen and (min-width: 1200px) {
          display: grid;
          grid-template-columns: 0.88fr 0.8fr 1.145fr 0.745fr;
          grid-template-rows: 177px 195px 124px;
          grid-gap: 10px;

          .img-wrap {
            box-shadow: none;
            position: relative;

            // &:hover {

            // 	&::before {
            // 		opacity: 1;
            // 	}
            // }

            // &::before {
            // 	position: absolute;
            // 	top: 0;
            // 	left: 0;
            // 	display: block;
            // 	width: 100%;
            // 	height: 100%;
            // 	background-color: rgba($color-black, 0.4);
            // 	color: $color-white;
            // 	opacity: 0;
            // }

            &:nth-child(1) {
              grid-area: 1 / 1 / span 1 / span 3;

              // &::before { content: '950x300';}
            }
            &:nth-child(2) {
              grid-area: 2 / 1 / span 3 / span 2;

              // &::before { content: '450x450';}
            }
            &:nth-child(3) {
              grid-area: 2 / 3 / span 1 / span 2;
              // &::before { content: '600x315';}
            }
            &:nth-child(4) {
              grid-area: 3 / 3 / span 1 / span 1;

              // &::before { content: '300x170';}
            }
            &:nth-child(5) {
              grid-area: 1 / 4 / span 1 / span 1;

              // &::before { content: '225x280';}
            }
            &:nth-child(6) {
              grid-area: 3 / 4 / span 1 / span 1;

              // &::before { content: '300x250';}
            }

            img {
              box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
            }
          }
        }
      }
    }

    // Device style
    .device-wrap {
      --device-w: 0; // html set
      --device-h: 0; // html set
      --device-height: calc((var(--device-h) / var(--device-w)) * 100%);

      width: 100%;
      height: 0;
      overflow: hidden;
      background-color: rgb(248, 248, 248);
      border: 5px solid rgb(248, 248, 248);
      box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
      border-radius: 10px;
      margin: 20px auto 40px;
      position: relative;

      // pc
      @media screen and (min-width: 575.99px) {
        border: 10px solid rgb(248, 248, 248);
        margin: 30px auto 70px;
      }

      // Device size - PC
      &.pc-dev-wrap {
        padding-bottom: calc(var(--device-height) - 5px); /* adjust border */

        // pc
        @media screen and (min-width: 575.99px) {
          width: calc(100% - 20px); /* adjust border */
          box-sizing: content-box;
          padding-bottom: calc(var(--device-height) - 10px); /* adjust border */
        }
      }

      // Device size - Mobile
      &.mobile-dev-wrap {
        padding-bottom: calc(
          var(--device-height) - 17px
        ); /* adjust border + scroll */

        // pc
        @media screen and (min-width: 575.99px) {
          padding-bottom: calc(var(--device-height) - 30px); /* adjust border */
        }
      }

      // Device size - Video
      &.video-container {
        position: relative;
        padding-bottom: 56.25%;
        padding-top: 30px;
        height: 0;
        overflow: hidden;

        iframe,
        object,
        embed {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
      }

      .dev-outter-wrap {
        overflow-x: hidden;
        overflow-y: scroll;
        width: 100%;
        height: 0;
        position: relative;
        padding-bottom: var(--device-height);

        &.hide-scroll {
          /* pc */
          @media screen and (min-width: 575.99px) {
            width: calc(100% + 17px); /* adjust border */
          }
        }

        .dev-inner-wrap {
          width: 100%;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }

    // Device layout - Mobile

    // Device layout - Mobile -- 1 column
    .col-1-mobile-wrap {
      width: 300px;
      margin: auto;

      @media screen and (max-width: 575.98px) {
        width: 55%;
      }
    }

    // Device layout - Mobile -- 2 column
    .col-2-mobile-wrap {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 20px;

      @media screen and (max-width: 575.98px) {
        grid-column-gap: 8px;
      }
    }

    // Tabs and Navigation - Nav
    ul.tab-nav-wrap {
      width: 100%;
      padding: 0;
      margin: 0;
      list-style-type: none;
      display: inline-flex;
      border-bottom: 1px solid $color-brown;

      // Tabs and Navigation - Nav -- mobile layout
      @media screen and (max-width: 575.98px) {
        position: sticky;
        top: 55px;
        z-index: 200;
        background-color: $color-white;
      }

      &::before,
      &::after {
        // Tabs and Navigation - Nav -- mobile layout
        @media screen and (max-width: 575.98px) {
          content: "";
          border-bottom: 1px solid $color-brown;
          box-sizing: content-box;
          background-color: $color-white;
          display: block;
          width: 25px;
          height: 100%;
          position: absolute;
          top: 0;
        }
      }

      &::before {
        // Tabs and Navigation - Nav -- mobile layout
        @media screen and (max-width: 575.98px) {
          left: -25px;
        }
      }

      &::after {
        // Tabs and Navigation - Nav -- mobile layout
        @media screen and (max-width: 575.98px) {
          right: -25px;
        }
      }

      .tab-nav {
        cursor: pointer;
        border: 1px solid $color-brown;
        border-bottom: none;
        padding: 5px 10px 2px;
        margin: 0;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        background-color: $color-white;
        color: $color-brown;
        font-size: 1.125rem;
        transition: 0.3s;

        // Tabs and Navigation - Nav -- mobile layout
        @media screen and (max-width: 575.98px) {
          padding: 10px 10px 4px;
          border-top-left-radius: unset;
          border-top-right-radius: unset;
          border-bottom: 1px solid $color-brown;
          font-size: 1rem;
          text-align: center;
          line-height: 1.3;
          border: unset;
          color: rgba($color-black, 0.3);
        }

        @media screen and (max-width: 375px) {
          font-size: 0.875rem;
        }

        @media screen and (max-width: 320px) {
          font-size: 0.75rem;
        }

        &.active {
          background-color: $color-brown;
          color: #fff;

          // Tabs and Navigation - Nav -- mobile layout
          @media screen and (max-width: 575.98px) {
            color: $color-brown;
            background-color: transparent;
            font-weight: 500;
          }
        }

        br {
          &.mobile-style {
            display: none;

            @media screen and (max-width: 575.98px) {
              display: block;
            }
          }
        }
      }

      .tab-nav + .tab-nav {
        margin: 0 0 0 10px;

        @media screen and (max-width: 575.98px) {
          margin: 0 0 0 5px;
        }
      }
    }

    // Tabs and Navigation - Content
    .tab-content-wrap {
      margin: 50px 0;

      .tab-content {
        display: none;

        &.active {
          display: block;
        }
      }

      &.fade-tab {
        .tab-content {
          &.active {
            animation-name: fade-in;
            animation-duration: 0.3s;
            animation-timing-function: linear;
            animation-iteration-count: 1;
          }
        }
      }
    }

    // Tabs and Navigation - Animation
    @keyframes fade-in {
      from {
        opacity: 0;
      }
      to {
        opacity: 1;
      }
    }


    // Link Card
    .card {

      & + .card {
        margin-top: 1.25rem;
      }
      
      &.link-card {
        // opacity: 1;
        // text-decoration: none;

        display: flex;
        flex-direction: row;
        gap: 1.5rem;
        padding: 1.25rem;
        border-radius: 5px;
        transition: .3s;

        @media screen and (max-width: 575.98px) {
          padding: 1rem;
          gap: 1rem;
        }

        &:hover, &:active {
          box-shadow: 0 2px 4px 0 rgba(36, 36, 36, 0.2);

          .arrow {
            transform: translateX(0.25rem);
          }
        }

        .card-image {
          width: 3.5rem;
          height: 3.5rem;

          @media screen and (max-width: 575.98px) {
            width: 2.5rem;
            height: 2.5rem;
          }

          img {
            width: 100%;
            height: 100%;
            object-position: center;
            object-fit: contain;
          }
        }

        .card-body {
          padding: 0;
          position: relative;

          .card-title {
            margin: 0;
            font-size: 1.125rem;
            font-weight: 500;
            line-height: 1.2;

            @media screen and (max-width: 575.98px) {
              font-size: 1rem;
            }
          }

          .card-des {
            margin: 0.625rem 0 0 0;
            font-size: 1rem;
            font-weight: 400;
            line-height: 1.2;
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            @media screen and (max-width: 575.98px) {
              font-size: 0.875rem;
            }

            * {
              margin: 0;
            }
          }

          .counter {
            position: absolute;
            right: -0.25rem;
            top: -0.25rem;
            opacity: 0.5;
            font-size: 1.875rem;
            font-weight: bold;
            line-height: 1;
            color: #EFE6DC;
            pointer-events: none;
            z-index: 0;
          }
        }

        .arrow {
          display: flex;
          align-items: center;
          justify-content: center;
          transition: .3s;

          .image-wrap {
            display: block;
            width: 2.5rem;
            height: 2.5rem;

            img {
              width: 100%;
              height: 100%;
              object-position: center;
              object-fit: contain;
            }
          }
        }
      }
    }
}
</style>