<template>
  <section>
    <GlobalNavbar>
      <template slot="menu">
        <UIMenu/>
      </template>
    </GlobalNavbar>

    <SectionAuthor />
    <UIListFilter
      :tagsStateObj="tagsStateObj"
      @updateFilterTags="updateFilterTags"
    />
    <SectionList :filterDataObj="filterDataObj" />
  </section>
</template>

<script>
// import components
import GlobalNavbar from '../components/global/GlobalNavbar.vue'
import UIMenu from '../components/global/UIMenu.vue'
import SectionAuthor from "../components/dashboard/SectionAuthor.vue";
import SectionList from "../components/dashboard/SectionList.vue";
import UIListFilter from "../components/dashboard/UIListFilter.vue";

export default {
  name: "PageDashboard",
  components: {
    GlobalNavbar,
    UIMenu,
    SectionAuthor,
    SectionList,
    UIListFilter,
  },
  data() {
    return {
      tagsStateObj: {
        html: { name: "HTML5", active: true },
        htmlemail: { name: "HTML Email", active: true },
        css: { name: "CSS", active: true },
        scss: { name: "SCSS", active: true },
        bootstrap: { name: "Bootstrap 5", active: true },
        javascript: { name: "JavaScript", active: true },
        jquery: { name: "jQuery 3", active: true },
        vue2: { name: "Vue 2", active: true },
        vue3: { name: "Vue 3", active: true },
        owlcarousel: { name: "Owl Carousel 2", active: true },
        slick: { name: "Slick Slide", active: true },
        gulp: { name: "Gulp", active: true },
        squarespace: { name: "Squarespace", active: true },
        wordpress: { name: "WordPress.org", active: true },
        photoshop: { name: "Adobe Photoshop", active: true },
        illustrator: { name: "Adobe Illustrator", active: true },
        xd: { name: "Adobe XD", active: true },
        all: { name: "All", active: true },
        remove_all: { name: "Remove All", active: false },
      },
    };
  },
  mounted() {
    
  },
  methods: {
    // 更新篩選標籤 (回調)
    updateFilterTags(obj_key) {
      if (obj_key == "remove_all") {
        Object.keys(this.tagsStateObj).forEach((key) => {
          this.tagsStateObj[key].active = false;
        });
        this.tagsStateObj.remove_all.active = true;
      } else if (obj_key == "all") {
        Object.keys(this.tagsStateObj).forEach((key) => {
          this.tagsStateObj[key].active = true;
        });
        this.tagsStateObj.remove_all.active = false;
      } else {
        // 若此標籤已存在, 刪除它 ; 反之
        this.tagsStateObj[obj_key].active = !this.tagsStateObj[obj_key].active;
      }
    },
  },
  computed: {
    // 計算屬性: store的state
    originDataObj() {
      return this.$store.state.dataObj
    },
    // 計算屬性: 已選取的標籤
    selectedTagsArr() {
      let arr = [];

      Object.keys(this.tagsStateObj).forEach((key) => {
        if (this.tagsStateObj[key].active) {
          arr.push(this.tagsStateObj[key].name);
        }
      });
      return arr;
    },
    // 計算屬性: 篩選後的作品
    filterDataObj() {
      // 複製一份
      let filKinds = JSON.parse(JSON.stringify(this.originDataObj));

      // 依序取出分類
      for (let i = 0; i < filKinds.length; i++) {
        // 此分類中, 符合條件的作品
        let filWorks = [];

        // 依序取出作品
        for (let j = 0; j < filKinds[i].works.length; j++) {
          // 依序取出作品中的每一個 tag, 進行篩選
          let isInclude = filKinds[i].works[j].tags.some((tag) => {
            // 此 tag 包含在選取的 filter tag 中?
            return this.selectedTagsArr.indexOf(tag) >= 0;
          });

          // 加入符合條件的作品
          if (isInclude) {
            filWorks.push(filKinds[i].works[j]);
          }
        }

        // 更新此分類中, 符合條件的作品
        filKinds[i].works = filWorks;
      }

      return filKinds;
    },
  },
  watch: {
    // 監視 selectedTagsArr 屬性
    selectedTagsArr(newValue) {
      // 手動取消所有標籤, 沒有項目時
      if (newValue.length == 0) {
        this.tagsStateObj.remove_all.active = true; // 選取 Remove All
      }

      // 手動取消所有標籤, 只剩 All 選取時
      if (newValue.length == 1 && newValue[0] == "All") {
        this.tagsStateObj.all.active = false; // 取消 All
        this.tagsStateObj.remove_all.active = true; // 選取 Remove All
      }

      // 手動取消任一標籤, 但 All 還選取時
      if (newValue.length < Object.keys(this.tagsStateObj).length - 1 && newValue.indexOf("All") != -1 ) {
        this.tagsStateObj.all.active = false; // 取消 All
      }

      // 在無選取狀態下, 手動選取任一標籤, 但 Remove All 還選取時
      if (newValue.length > 1 && newValue.indexOf("Remove All") != -1) {
        this.tagsStateObj.remove_all.active = false; // 取消 Remove All
      }

      // 手動選擇所有標籤, 但 All 未選時
      if (newValue.length == Object.keys(this.tagsStateObj).length - 2 && newValue.indexOf("All") == -1) {
        this.tagsStateObj.all.active = true; // 選取 All
      }
    },
  },
};
</script>

