import VueRouter from 'vue-router'

// 引入路由組件
import PageDashboard from '../pages/PageDashboard'
import PageDetail from '../pages/PageDetail'
import PageNotFound from '../pages/PageNotFound'

// 引入 store
import store from '../store/'

const router = new VueRouter({
    // mode: 'history', // 佈署在github page上, 絕對要用hash模式, 不然子頁面只要重整或直接導向都會404
    base: '/joyxie-works/',
    routes: [
        {
            path: '/',
            component: PageDashboard
        },
        {
            path: '/:category/:name', //pamas參數佔位符
            component: PageDetail,
            props: true, //使用props接收params參數
            beforeEnter: (to, from, next) => { //獨享路由守衛
                let category = to.params.category
                let name = to.params.name
                
                // 檢查是否有這個分類
                let kindArr = store.state.dataObj.filter((kind)=>{
                    return kind.category === category
                })

                // console.log(performance.navigation.type)
                if (kindArr.length <= 0) {
                    console.log(kindArr.length)
                        next({ name: 'not-found' })
                } else {
                    let workArr = kindArr[0].works.filter((work)=>{
                        return work.name === name
                    })

                    // 檢查是否有這個作品
                    if (workArr.length <= 0) {
                        // 導向 not-found
                        next({ name: 'not-found' })
                    }
                }

                // 路由繼續前進
                next()
            }
        },
        {
            path: '/not-found',
            name: 'not-found',
            component: PageNotFound
        },
        {
            path: '*',
            redirect: '/not-found'
        }
    ],
})


//全局後置路由守衛---初始化的時候被調用、每次路由切換之後被調用
router.afterEach(() => {
  // 滾動條始終在頂部
  window.scrollTo({
    top: 0,
    left: 0,
    behavior: 'instant' // 瞬間移動
  })
});


export default router