<template>
  <nav class="nav-wrap">
    <div class="container">
        <div class="row">
            <div class="col-12">
                <nav class="nav-inner-wrap">
                    <div class="nav-left">
						<!-- back slot -->
						<slot name="back"></slot>
                        
						<div class="title-wrap">
							<a @click="scrollTop">{{ title }}</a>
                        </div>
                    </div>

					<!-- menu slot -->
					<slot name="menu"></slot>

                    <div class="nav-right">
                        <div class="contact-wrap">
                            <a :href="github" target="_blank" class="link-icon icon-github" title="Github" aria-label="Github"></a>
                            <a :href="'mailto:' + email" class="link-icon icon-mail" title="Email" aria-label="Email"></a>
                        </div>
                    </div>
                </nav>
            </div>
        </div>
    </div>
  </nav>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
  name: 'GlobalNavbar',
  data () {
    return {
      title: 'Joy Xie Works',
      github: 'https://github.com/MinRuXie',
      email: 'a556987@gmail.com'
    }
  },
  mounted() {

  },
  methods: {
    // 捲動至頂端
    scrollTop() {
      $("html, body").animate({
        scrollTop: 0,
      },0);
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../assets/scss/variables";

.nav-wrap {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 500;
	width: 100vw;
	height: 60px;
	line-height: 60px;
	box-shadow: 0 2px 4px 0 rgba($color-black, 0.1);
	background-color: $color-white;

	@media screen and (max-width: 575.98px) {
		height: 55px;
		line-height: 55px;
		padding: 0 10px;
	}

	.nav-inner-wrap {
		display: flex;
		align-items: center;
		justify-content: space-between;

		// Navigation - link
		a {
			color: $color-black;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}
		}

		// Navigation - left
		.nav-left {
			display: flex;
	
			// Navigation - go back
			.goback-btn {
				display: block;
				margin-right: 10px;
				cursor: pointer;
				text-decoration: none;
			}
			
			// Navigation - logo title
			.title-wrap {
				font-size: 1.5rem;
				font-weight: 500;

				@media screen and (max-width: 575.98px) {
					font-size: 1.375rem;
				}

				a {
					cursor: pointer;
					text-decoration: none;
				}
			}
		}  	
	
		// Navigation - right
		.nav-right {
	
			// Navigation - contact
			.contact-wrap {
				display: flex;
				align-items: center;
			
				.link-icon {
			
					&::before {
						width: 22px;
						height: 22px;
						opacity: 0.7;
					}
			
					&:hover {
						&::before {
							opacity: 1;
						}
					}
				}
			
				.link-icon + .link-icon {
					margin-left: 17px;
				}
			}
		}
	}
}
</style>
