<template>
  <div id="app">
    <!-- 淡入淡出動畫 -->
    <transition name="switch">
      <!-- 根據路由決定展示組件 -->
      <router-view></router-view>
    </transition>
    
    <GlobalFooter />

    <a class="top-btn" @click="scrollTop"></a>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

import GlobalFooter from "./components/global/GlobalFooter";

export default {
  name: "App",
  components: {
    GlobalFooter,
  },
  data() {
    return {

    }
  },
  mounted() {
    $(window).on('scroll', function() {
      let scrolled = $(this).scrollTop();

      if (scrolled <= 0) {
        $('.top-btn').addClass('hide');
      } else {
        $('.top-btn').removeClass('hide');
      }
    })
  },
  methods: {
    // 捲動至頂端
    scrollTop() {
      $("html, body").animate({
        scrollTop: 0,
      },0);
    },
  },
};
</script>

<style lang="scss">
@import "~bootstrap/scss/bootstrap";
@import "./assets/scss/mixin";
@import "./assets/scss/variables.scss";

// General
* {
  font-family: "Roboto", "PingFang TC", "Noto Sans TC", sans-serif;
  color: $color-black;
  -webkit-font-smoothing: antialiased;
}

html,
body {
  margin: 0;
  padding: 0;

  min-width: 320px;
}

body {
  &.modal-open {
    overflow-y: scroll;
  }
}

h1 {
  font-size: 3.125rem;
  font-weight: 500;
  line-height: 1;
}

// Content
.content-page {
  padding-top: 60px;
}

//---------------
// Tags
//---------------
.tag-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;

  .tag-item {
    display: inline-block;
    border: 1px solid $color-light-brown;
    border-radius: 4px;
    padding: 5px;
    font-size: 0.75rem;
    font-weight: 500;
    color: $color-brown;
    text-align: center;
    line-height: 1;
    margin-right: 5px;
    margin-bottom: 5px;
  }
}

//---------------
// Link icon
//---------------
.link-icon {
  &::before {
    content: "";
    display: block;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  // Link icon - github
  &.icon-github::before {
    background-image: url("./assets/images/ion-social-github-ionicons.svg");
  }

  // Link icon - website
  &.icon-website::before {
    background-image: url("./assets/images/globe.svg");
  }

  // Link icon - mail
  &.icon-mail::before {
    background-image: url("./assets/images/email-material.svg");
  }

  // Link icon - demo
  &.icon-demo::before {
    background-image: url("./assets/images/view.svg");
  }
}

/* Top Button */
.top-btn {
  position: fixed;
  bottom: 15px;
  right: 15px;
  border: 1px solid $color-brown;
  border-radius: 100px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $color-white;
  opacity: 0.8;
  transition: 0.3s;
  text-decoration: none;
  cursor: pointer;

  //state
  &.hide {
    opacity: 0;
    visibility: hidden;
  }

  &::before {
    content: "TOP";
    color: $color-brown;
    font-size: 0.875rem;
    line-height: 1;
    transform: translateY(2px);
    transition: 0.3s;
  }

  &:hover {
    text-decoration: none;
    background-color: $color-brown;

    &::before {
      color: $color-white;
    }
  }
}


//---------------
// Animate
//---------------
.switch-enter-active {
  animation: fadeIn 0.2s;
}
.switch-leave-active {
  animation: fadeOut 0.2s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
</style>
