import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


// import data json
import dataObj from "../assets/data/works.json";

// import src image file
for (let i = 0; i < dataObj.length; i++) {
  for (let j = 0; j < dataObj[i].works.length; j++) {
    let image_path = dataObj[i].works[j].image;
    let image_mobile_path = dataObj[i].works[j].image_mobile;

    // add new dataObj field
    dataObj[i].works[j].import_image = require(`@/assets/images/work/${image_path}`);
    dataObj[i].works[j].import_image_mobile = require(`@/assets/images/work/${image_mobile_path}`);
  }
}



//準備actions: 用於響應組件中的動作
const actions = {

}

//準備mutations: 用於操作數據 (state)
const mutations = {

}

//準備state: 用於存儲數據
const state = {
    dataObj,
    sectionOffsetTop: []
}

//準備getters: 用於將state中的數據進行加工
const getters = {

}


//創建並導出 store
export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})