<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
    name: 'UIMenu',
	computed: {
		sectionOffsetTop() {
			return this.$store.state.sectionOffsetTop
		}
	},
	mounted() {
		
	},
	methods: {
		scrollTo(id) {
			let sectionArr = this.sectionOffsetTop.filter((section)=>{
				return section.id === id
			})

			let offsetTop = sectionArr[0].offsetTop
			
			$("html, body").animate({
				scrollTop: offsetTop - 80,
			},0);
		}
	}
}
</script>

<style lang="scss">
@import "../../assets/scss/variables";

// Works Dashboard Menu
.dashboard-menu-wrap {
	position: fixed;
	z-index: 600;
	top: 30px;
	left: 50%;
	transform: translate(-50%, -50%);
	list-style-type: none;
	padding: 0;
	margin: 0;

	@media screen and (max-width: 1199.98px) {
		display: none;
	}

	.dashboard-menu-item + .dashboard-menu-item {
		margin-left: 40px;
	}
	.dashboard-menu-item {
		display: inline-block;

		a {
			text-decoration: none;
            color: $color-black;
			opacity: 0.7;
			font-size: 0.875rem;
			letter-spacing: 0.3px;
			transition: .3s;
			display: inline-flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			position: relative;
			cursor: pointer;

			&:hover {
				color: $color-brown;
				text-decoration: none;

				&::after {
					opacity: 1;
				}
			}

			&::after {
				content: attr(data-en);
				display: block;
				width: 100%;
				text-align: center;
				word-break: keep-all;
				white-space: pre;
				background-color: $color-white;
				opacity: 0;
				transition: .3s;
				position: absolute;
			}
		}
	}
}
</style>