<template>
    <section class="dashboard-section">
        
        <!-- 分類 -->
        <div class="container" v-for="kind in filterDataObj" :key="kind.category">

            <template v-if="kind.show">
                <div class="row">
                    <div class="col-12 col-lg-12 col-md-12">
                        <UIListHeader :kind="kind"/>
                    </div>
                </div>

                <div class="row">
                    <template v-for="work in kind.works">
                        <template v-if="work.show">

                            <div class="col-12 col-lg-3 col-md-4 dashboard-card-wrap" :key="work.name">
                                <!-- 直接外開連結 -->
                                <template v-if="work.external_link != ''">
                                    <a :href="work.external_link" :title="`另開${work.title}`" :aria-label="`另開${work.title}`" target="_blank">
                                        <UIListItem :work="work"/>
                                    </a>
                                </template>

                                <!-- 有文章內頁 -->
                                <template v-else>
                                    <!-- Vue中借助router-link標籤實現路由的切換 -->
                                    <router-link :to="`/${kind.category}/${work.name}`" :title="`前往${work.title}詳細頁`" :aria-label="`前往${work.title}詳細頁`">
                                        <UIListItem :work="work"/>
                                    </router-link>
                                </template>
                            </div>

                        </template>
                    </template>
                </div>
            </template>

           
        </div>
    </section>
</template>

<script>
// import components
import UIListHeader from './UIListHeader'
import UIListItem from './UIListItem'

export default {
    name: 'SectionList',
    components: {
        UIListHeader,
        UIListItem,
    },
    props: ['filterDataObj']
}
</script>

<style lang="scss">
@import "../../assets/scss/variables";

.dashboard-section {
	a {
		text-decoration: none;
	}
}
</style>