<template>
  <div>
    <GlobalNavbar>
      <template slot="back">
        <router-link class="goback-btn" to="/">←</router-link>
      </template>
    </GlobalNavbar>

    <div class="container d-none d-sm-block">
      <DetailHeader :showDataObj="showDataObj" />
    </div>

    <div class="detail-body">
      <div class="container">
        <div class="row">
          <div class="col-lg-8 col-sm-12">
            <DetailArticle :currentArticle="name" />
          </div>
          <div class="col-lg-4 col-sm-12 order-first order-sm-0">
            <DetailSidebar :showDataObj="showDataObj" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

// import components
import GlobalNavbar from "../components/global/GlobalNavbar.vue";
import DetailHeader from "../components/detail/DetailHeader";
import DetailSidebar from "../components/detail/DetailSidebar";
import DetailArticle from "../components/detail/DetailArticle";

export default {
  name: "PageDetail",
  props: ['category', 'name'],
  components: {
    GlobalNavbar,
    DetailHeader,
    DetailSidebar,
    DetailArticle,
  },
  data() {
    return {

    };
  },
  computed: {
    // 計算屬性: store的state
    originDataObj() {
      return this.$store.state.dataObj
    },
    // 計算屬性: 尋找對應的作品
    showDataObj() {
      // 尋找對應分類
      let kindArr = this.originDataObj.filter((kind)=>{
        return kind.category === this.category
      })

      // 尋找對應的作品
      let workArr = kindArr[0].works.filter((work)=>{
        return work.name === this.name
      })

      return workArr[0]
    }
  },
  mounted() {
    //修改網頁title
    document.title = `${this.showDataObj.title} | JoyXie 作品集`
  },
  beforeDestroy() {
    //修改網頁title
    document.title = 'JoyXie 作品集'
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import "../assets/scss/variables";

// Works Detail - Body
.detail-body {
  position: relative;

  @media screen and (max-width: 575.98px) {
    top: 70px;
  }
}
</style>
